<template>
  <div id="showblogs">
    <h1>博客总览</h1>
    <input type="text" placeholder="search" v-model="search">
    <div class="single-blog" v-for="(item,index) in filterblogs" :key="index">
      <router-link :to="'/blog/'+item.id" tag="h3" v-rainbow>{{item.title | touppercase}}</router-link>
      <article>{{item.body | snipet}}</article>
    </div>
  </div>
</template>

<script>
export default {
  name: "showblog",
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  created() {
    this.$http.get("http://jsonplaceholder.typicode.com/posts").then(data => {
      this.blogs = data.body.slice(0, 10);
    });
  },
  filters: {
    touppercase,
    snipet
  },
  computed: {
    filterblogs() {
      return this.blogs.filter(item => {
        return item.title.match(this.search);
      });
    }
  },
  directives: {
    rainbow: {
      bind(el, binding, vnode) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(4, 10);
      }
    }
  }
};
function touppercase(value) {
  return value.toUpperCase();
}
function snipet(value) {
  if (value.length > 100) return value.slice(0, 200) + "...";
}
</script>

<style>
#showblogs {
  max-width: 800px;
  margin: 0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background-color: #eee;
  border: 1px dotted #aaa;
}

h3 {
  cursor: pointer;
  transition: all 0.5s;
}
h3:hover {
  transform: scale(1.02);
}
input[type="text"] {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
  margin-top: 20px;
}
</style>



