<template>
  <div id="addblog">
    <h2>添加博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required>

      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxes">
        <input id="Vue.js" type="checkbox" value="Vue.js" v-model="blog.categories">
        <label for="Vue.js">Vue.js</label>

        <input id="Node.js" type="checkbox" value="Node.js" v-model="blog.categories">
        <label for="Node.js">Node.js</label>

        <input id="React.js" type="checkbox" value="React.js" v-model="blog.categories">
        <label for="React.js">React.js</label>

        <input id="Angular.js" type="checkbox" value="Angular.js" v-model="blog.categories">
        <label for="Angular.js">Angular.js</label>
      </div>

      <label>作者</label>
      <select v-model="blog.author">
        <option v-for="(item,index) in authors" :key="index">{{item}}</option>
      </select>

      <button @click.prevent="post">添加博客</button>
    </form>

    <div v-if="submmited">
      <h3>博客发布成功!</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题:{{blog.title}}</p>
      <p>博客内容:</p>
      <p>{{blog.content}}</p>
      <p>分类:</p>
      <ul>
        <li v-for="(item,index) in blog.categories" :key="index">{{item}}</li>
      </ul>
      <p>作者:{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "addblog",
  data() {
    return {
      blog: {
        title: "",
        content: "",
        categories: [],
        author: ""
      },
      authors: ["OuYanghao", "Aimee", "Hemiah", "Henry"],
      submmited: false
    };
  },
  methods: {
    post() {
      this.$http
        .post("http://jsonplaceholder.typicode.com/posts", {
          title: this.blog.title,
          body: this.blog.content,
          userId: 1
        })
        .then(data => {
          console.log(data);
          this.submmited = true;
        });
    }
  }
};
</script>

<style scoped>
#addblog * {
  box-sizing: border-box;
}
#addblog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
label {
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea {
  height: 150px;
}
#checkboxes label {
  display: inline-block;
  margin-top: 0;
}
#checkboxes input {
  display: inline-block;
  margin-left: 15px;
}
#checkboxes input:first-child {
  margin-left: 0;
}

button {
  display: block;
  margin: 20px 0;
  background: crimson;
  color: white;
  border: 0;
  padding: 14px;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
}

#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3 {
  margin-top: 10px;
}

#preview p {
  margin-top: 10px;
}
</style>



